body {
  font-family: var(--font-family);
  --header-height: 60px;
  --main-color: #00acdc;
  --font-family: Roboto, sans-serif;
  margin: 0;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.nav__menu {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__menu_item_link {
  height: var(--header-height);
  text-transform: uppercase;
  color: #000;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__menu_item_link:not(.first) {
  margin-left: 20px;
}

.nav__menu_item_link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__menu_logo {
  width: 40px;
  height: 40px;
  display: block;
}

